.btn {
  display: block;
  padding: 0;
  font-size: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &:focus {
    outline-width: inherit;
    outline-color: transparent;
  }

}

.btn--brown {
  font-family: 'Roboto', "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.14;
  text-align: center;
  text-transform: uppercase;
  color: @color-white;
  padding: 19px 40px;
  background-color: @color-brown;
  border-radius: 3px;

  &:hover {
    background-color: @color-light-brown;
    cursor: pointer;
  }

}