.swiper-container {
  width: 100%;
  height: auto;
  margin-top: -100px;
  z-index: -5;

  // @media(min-width: @width-tablet) {
  //   height: 300px;
  // }

  // @media(min-width: @width-desktop) {
  //   height: 300px;
  // }
}

.swiper-button-prev,
.swiper-button-next {
  display: none;

  @media(min-width: @width-tablet) {
    display: block;
  }
}

.slide {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: @color-dark;


  // @media(min-width: @width-tablet) {
  //   padding-top: 200px;
  //   padding-bottom: 100px;
  // }

  // @media(min-width: @width-desktop) {
  //   padding-top: 409px;
  //   padding-bottom: 312px;
  // }

}

.slide__img {
  width: 100%;
  height: 100%;
}

.slide__text-box {
  position: absolute;
  top: 120px;
  left: auto;
  right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 100px;

  @media(min-width: @width-tablet) {
    top: 250px;
  }

  @media(min-width: @width-desktop) {
    top: 409px;
  }
}

.slide__subtitle {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.17;

  @media(min-width: @width-tablet) {
    font-size: 30px;
  }

  @media(min-width: @width-desktop) {
    font-size: 36px;
  }
}

.slide__title {
  margin-bottom: 5px;
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 1;

  @media(min-width: @width-tablet) {
    font-size: 40px;
  }

  @media(min-width: @width-desktop) {
    font-size: 52px;
  }
}

.slide__text {
  width: 60%;
  margin-bottom: 40px;
  font-family: "montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  color: @color-white;

  // @media(min-width: @width-tablet) {
  //   width: 563px;
  // }

  @media(min-width: @width-desktop) {
    width: 563px;
  }
}

.slide__btn {
  margin-bottom: 100px;

  @media(min-width: @width-tablet) {
    margin-bottom: 200px;
  }

  @media(min-width: @width-desktop) {
    margin-bottom: 312px;
  }
}






