.title {
  margin-bottom: 35px;
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 1;
  color: @color-dark;
  text-transform: capitalize;
  text-align: center;

  @media(min-width: @width-tablet) {
    font-size: 30px;
  }
}

